    private static readonly SEARCHBOX_HEIGHT;
    /**
     * 下拉面板的高度，默认展示8条
     */
    dropMaxHeight: number;
    /**
     * @ignore 搜索结果
     */
    oldOptions: Array<any>;
    /**
     * @ignore 内部标签
     */
